<template>
  <div class="box">
    <div class="box_mask">
      <div class="menu" :class="[show_menu?'open':'']">
        <div>
          <div class="close_menu" @click="show_menu=!show_menu">
            x
          </div>
          <div class="avator_box">
            <a href="#">
              <img src="../assets/logo.png" />
              <span></span>
            </a>
          </div>
          <div class="user_info">
            <h4>Mr.Fan</h4>
            <span>Web Designer</span>
          </div>
          <nav class="main-nav">
            <ul class="main-menu">
              <li @click="show_menu? show_menu=!show_menu:''">
                <router-link to="/about_me" active-class="active" exact>
                  About Me
                </router-link>
              </li>
              <li @click="show_menu? show_menu=!show_menu:''">
                <router-link to="/skill" active-class="active" exact>
                  Professional Skill
                </router-link>
              </li>
              <li @click="show_menu? show_menu=!show_menu:''">
                <router-link to="/work_experience" active-class="active" exact>
                  Work Experience
                </router-link>
              </li>
              <li @click="show_menu? show_menu=!show_menu:''">
                <router-link to="/contact_me" active-class="active" exact>
                  Contact Me
                </router-link>
              </li>
            </ul>
          </nav>
        </div>
        <div class="copyright-text">
          <p>
            Copyright 2020 Mr.Fan Design.
          </p>
        </div>
      </div>
      <div class="content">
        <div class="content_header">
          <span class="menu_button" @click="show_menu=!show_menu">
            #
          </span>
          <span class="go">
            <router-link to="/">返回</router-link>
          </span>
        </div>
        <div class="content_box">
          <transition name="slide-fade">
            <router-view></router-view>
          </transition>
        </div>
      </div>




      <!-- <p>
      	用户名: <input v-model="temp.username"  />
      </p>
      <p>
      	密  码: <input v-model="temp.password"  />
      	<button @click="login">登录</button>
      	<button>注册</button>
      </p>
      <p>
      	用户id: <input name="user_id"  />
      	<button>信息</button>
      </p>

      <p>
      	<button>退出</button>
      </p> -->
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    name: 'index',
    data() {
      return {
        temp: {
          username: '',
          password: ''
        },
        show_menu:false,
      }
    },
    methods: {
      login() {
        console.log('登录')
        axios.post('http://localhost:3000/admin/user/login', this.temp).then(response => {
          console.log(response)
        }).catch(err => {
          console.log(err)
        })
      },
    }
  }
</script>

<style scoped>
  .close_menu {
    display: none;
    height: 40px;
    line-height: 40px;
    text-align: center;
    background-color: #fff;
    color: #A43F49;
    cursor: pointer;
  }
  .box {
    background-image: url(../assets/bg.png);
    background-repeat: no-repeat;
    background-size: cover;
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
  }

  .box_mask {
    position: fixed;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, .5);
  }

  .menu {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 20%;
    height: 100%;
    background-color: #a43f49;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .avator_box {
    margin-top: 60px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }

  .avator_box a {
    text-decoration: none;
    color: inherit;
    display: block;
    position: relative;
    transform: scale(.8);
  }
  .avator_box a:hover {
    animation-duration: .3s;
    animation-timing-function: ease-out;
    animation-fill-mode: both;
    animation-name: scale-up;
  }
  .avator_box a span {
    display: block;
    width: 160px;
    height: 160px;
    border-radius: 50%;
    background-color: #fff;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
  }
  .avator_box a img {
    width: 160px;
    height: 160px;
    border-radius: 50%;
    text-align: center;
  }

  .user_info {
    margin: 40px 0px 60px 0px;
    text-align: center;
  }

  .user_info h4 {
    font-size: 26px;
    font-weight: 700;
    color: #fff;
    letter-spacing: 0.25px;
  }

  .user_info span {
    font-size: 13px;
    font-style: italic;
    color: #fff;
  }

  .main-nav {
    text-align: center;
  }

  .main-menu {
    display: flex;
    flex-direction: column;
  }

  ul {
    padding: 0;
    margin: 0;
    list-style: none;
    display: block;
  }

  .main-nav li {
    display: inline-block;
  }

  .main-nav li:hover a,
  .main-nav li.active a {
    background: #fff;
    color: #8c363e ;
  }
  .router-link-exact-active, .active {
    background: #fff;
    color: #8c363e !important;
  }
  .main-nav li a {
    height: 70px;
    line-height: 70px;
    display: inline-block;
    font-size: 16px;
    font-weight: 700;
    color: #fff;
    width: 100%;
    transition: all 0.5s;
    border-top: 2px solid #fff;
    text-decoration: none;
  }

  .main-nav li:last-child a {
    border-bottom: 2px solid #fff;
  }

  .copyright-text {
    bottom: 0;
    width: 100%;
  }

  .copyright-text p {
    text-align: center;
    border-top: 1px solid rgba(250, 250, 250, 0.1);
    display: inline-block;
    width: 100%;
    padding-top: 30px;
    font-size: 13px;
    color: #fff;
  }

  .content {
    position: fixed;
    width: 80%;
    height: 100%;
    box-sizing: border-box;
    right: 0;
    top: 0;
    padding: 0px 50px;
    display: flex;
    flex-direction: column;
    overflow: hidden;
  }
  .content_header {
    width: 100%;
    padding: 10px 0px;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
  }
  .content_box {
    width: 100%;
    flex: 1;
    box-sizing: border-box;
    background-color: rgba(250,250,250,.2);
    overflow-x: hidden;
    overflow-y: auto;
  }

  .slide-fade-enter-active {
    transition: all 1.5s ease;
  }
  .slide-fade-leave-active {
    transition: all .1s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }
  .slide-fade-enter, .slide-fade-leave-to
  /* .slide-fade-leave-active for below version 2.1.8 */ {
    transform: translateX(10px);
    opacity: 0;
  }
  .menu_button {
    display: none;
  }
  .go,.menu_button {
    padding: 8px 15px;
    border: 1px solid #fff;
    cursor: pointer;
    transform: scale(.8);
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
    color: #fff;
    border-radius: 4px;
   background-color: #8c363e;
  }
  .go a,.menu_button a {
    display: block;
    width: 100%;
    height: 100%;
    background-color: #8C363E !important;
    color: #fff !important;
    text-decoration: none;
  }
  .go:hover ,.menu_button:hover{
    animation-duration: .3s;
    animation-timing-function: ease-out;
    animation-fill-mode: both;
    animation-name: scale-up;
  }
  @keyframes scale-up {
      0% {
          transform: scale(.8)
      }

      100% {
          transform: scale(1)
      }
  }
  @keyframes slide-left {
      0% {
          opacity: 0;
          transform: translateX(-100%)
      }

      100% {
          opacity: 1;
          transform: translateX(0)
      }
  }

  @media (max-width: 768px) {
    .menu_button {
      display: block;
    }
    .open {
      display: flex;
      transform: translateX(0) !important;
      transition: transform 233ms;
    }
    .close_menu {
      display: block;
    }
    .avator_box {
      margin-top: 20px;
    }
    .menu {
      z-index: 22;
      width: 50%;
      transform: translateX(-100%);
      transition: transform 233ms;
    }
    .content {
      width: 100%;
      padding: 0px 20px;
    }
    .main-nav li a {
      height: 4em;
      line-height: 4em;
    }
    .avator_box a span {
      width: 10em;
      height: 10em;
    }
    .avator_box a img {
      width: 10em;
      height: 10em;
    }
    .content_header {
      justify-content: space-between;
    }
    .main-nav {
      padding: 0px 15px;
    }
  }
  @media (max-width: 414px) {
    .menu_button {
      display: block;
    }
    .open {
      display: flex;
      transform: translateX(0) !important;
      transition: transform 233ms;
    }
    .close_menu {
      display: block;
    }
    .avator_box {
      margin-top: 20px;
    }
    .menu {
      z-index: 22;
      width: 50%;
      transform: translateX(-100%);
      transition: transform 233ms;
    }
    .content {
      width: 100%;
      padding: 0px 20px;
    }
    .main-nav li a {
      height: 2.5em;
      line-height: 2.5em;
    }
    .avator_box a span {
      width: 6em;
      height: 6em;
    }
    .avator_box a img {
      width: 6em;
      height: 6em;
      border-radius: 50%;
      text-align: center;
    }
    .user_info {
      margin: 0px 0px 30px 0px;
      text-align: center;
    }
    .copyright-text p {
      padding-top: 1em;
    }
    .main-nav {
      padding: 0px 15px;
    }
  }
</style>



